.CobitHeader{
    width: 100%;
    min-height: 150px;
    height: 30%;
    padding: 10px;
    position: sticky;
    top: 0px;
    z-index: 10;

    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;

    /* background: linear-gradient(to bottom, var(--darker-color) 60%, var(--main-color)); */
    background-color: var(--transparent-darker-color);
    box-shadow: 0px 0px 10px rgba(0,0,0,.5);
    color: white;
}

.Titulos{
    font-size: 5rem;
    flex-grow: 1;
    margin-left: 10px;
    line-height: 0px;
}
.Titulos h3{
    font-size: 1em;
    line-height: 1em;
    font-family: 'Teko', sans-serif;
}
.Titulos h4{
    display: inline-block;
    border-top: 1px solid white;
    font-size: 0.5em;
    line-height: 1em;
}
.CobitHeader .LogoContainer{
    height: 100%;
    flex-basis: 40%;
    max-width: 250px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.1);
}

@media(max-width: 959px){
    .Titulos{
        font-size: 4.4rem;
    }
}

@media(max-width: 719px){
    .CobitHeader{
        height: 25%;
    }
    .Titulos{
        font-size: 3.5rem;
    }
}

@media(max-width: 500px){
    .CobitHeader{
        height: 25%;
    }
    .Titulos{
        font-size: 14vw;
    }
}