.ButtonsContainer{
    flex: 0 0 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.Button{
    flex-grow: 1;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: var(--main-color);
    color: white;
    font-size: 1.2rem;
    border: none;
    border-right: 1px solid white;

    cursor: pointer;
    transition: background .3s;
}

.Button:hover,
.Button:active{
    background-color: var(--darker-color);
}