.DialogueBubble_DialogueBubble__G735e{
    position: relative;
    max-width: 80%;
    font-size: 1rem;
    padding: 5px 15px;
    border-radius: 5px;
    box-sizing: border-box;
    white-space: pre-wrap;
}
.DialogueBubble_DialogueBubble__G735e::before{
    white-space: normal;
}
.DialogueBubble_UserBubble__xKh6F{
    align-self: flex-end;
    background-color: var(--main-color);
    color: white;
    border-top-right-radius: 0px;
}
.DialogueBubble_UserBubble__xKh6F::before{
    content: "";
    display: inline-block;
    position: absolute;
    top: 0px;
    left: 100%;
    border-top: 5px solid var(--main-color);
    border-bottom: 5px solid transparent;
    border-right: 5px solid transparent;
    border-left: 5px solid var(--main-color);
    border-top-right-radius: 3px;
}
.DialogueBubble_ChatbotBubble__2ZcS9{
    align-self: flex-start;
    border: 1px solid var(--main-color);
    color: var(--main-color);
    border-top-left-radius: 0;
}
.DialogueBubble_ChatbotBubble__2ZcS9::before{
    content: "";
    display: inline-block;
    position: absolute;
    top: -1px;
    right: 100%;
    border-top: 5px solid var(--main-color);
    border-bottom: 5px solid transparent;
    border-right: 5px solid var(--main-color);
    border-left: 5px solid transparent;
    border-top-left-radius: 3px;
}
