.MainPage {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100vh;
    overflow: auto;
    background-color: cornflowerblue;
  
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
  }
  .ChatContainer{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
  }
  
  .ButtonNavigator{
    display: none;
  }
  
  @media(max-width: 719px){
    .ButtonNavigator{
      display: flex;
      flex-flow: row nowrap;
      width: 200vw;
      height: 50px;
  
      /* box-shadow: 0 0px 5px rgba(0,0,0,.5); */
      z-index: 5;
      cursor: pointer;
    }
    .ButtonNavigator button{
      width: 50%;
      height: 100%;
      background-color: var(--darker-color);
      border: 1px solid whitesmoke;
  
  
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }
    .ButtonNavigator button:first-child{
      background-color: var(--main-color);
    }
    .ButtonNavigator button p,
    .ButtonNavigator button svg{
      color: white;
      font-size: 1.6rem;
      font-family: 'Open Sans', sans-serif;
      margin: 0px 10px;
    }
  
    .MainPage{
      min-height: unset;
      height: 100%;
    }
    .ChatContainer{
      height: calc(100% - 50px);
      width: 200vw;
      flex-direction: row-reverse;
    }
  }