.ChatDescription{
    width: 55%;
    min-height: 100%;
    overflow-y: auto;
    
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;

    background-color: var(--main-color);
    background-image: url("../../assets/pattern3.png");
    background-repeat: repeat;
    box-shadow: -2px 0px 5px rgba(0,0,0,.5);
}

.ChatDescription section{
    width: 95%;
    max-width: 500px;
    padding: 20px 0px;

    color: white;
}
.ChatDescription section p{
    font-size: 1.4rem;
}

@media(max-width: 719px){
    .ChatDescription{
        width: 50%;
        scroll-snap-align: center;
    }
}